  padding: 0px 20px;
  margin: 20px 0;
  padding-bottom: 15px;
  min-height: 450px;
}

.cartPage__body .top-left-corner {
  top: 5px;
  left: -15px;
}

.cartPage__body p {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.cartPage__body b {
  font-weight: 700;
}

