  margin-left: 30px;
  color: #48423b;
  font-size: 32px;
  font-weight: 700;
}

.cartPage__body table {
  margin-top: 10px;
  margin-bottom: 170px;
}

.cartPage__body table th {
  font-weight: 700;
}

.cartPage__body table tr {
  background-color: #fff;
}

.cartPage__body button {
  margin-top: 20px;
  height: 60px;
