.searchPage {
  color: #000;
}

.searchPage__container {
  background-color: #fff;
  padding: 15px 20px;
  margin: 25px 0;
}

.searchPage__container .topLeftCorner {
  top: -10px;
  left: -15px;
}

.searchPage__container img {
  max-width: 100%;
}

.searchPage__container input {
  width: 100%;
  padding: 5px;
  margin-bottom: 30px;
}

.searchPage__container button {
  width: 40%;
}

.searchPage__container ul {
  list-style: none;
}
