.footer {
  background-color: #727376;
  padding-top: 50px;
  color: #ffffff;
  font-size: 20px;
  font-weight: 400;
}

.footer p {
  font-weight: 400;
  white-space: nowrap;
}

.footer ul {
  list-style: none;
}

@media (max-width: 575.98px) {
  .footer ul {
    padding-left: 0;
  }
}

.footer img {
  margin-left: 10px;
  margin-top: 10px;
}

.footer h6 {
  font-weight: 700;
  font-size: 24px;
}

.footer a {
  text-decoration: none;
  color: #fff;
}
